.menu {
  width: 115px;
  height: 100vh;
  background: #625dee;
  border-radius: 0px 48px 0px 0px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  z-index: 10;
  left: 0;
  overflow: hidden;
  transition: all 0.5s ease;
}

.menu:hover {
  width: 420px;
}

.menu:hover .menu__list-items {
  width: 420px;
  transition: all 0.5s ease;
}

.menu:hover .menu__list-items-text {
  opacity: 1;
  transition: all 0.5s ease;
}

.menu__open {
  display: none;
  position: fixed;
  top: 45px;
  right: 40px;
  width: 30px;
  height: 30px;
  background-color: #625dee;
  cursor: pointer;
}

.menu__close {
  display: none;
  position: absolute;
  top: 45px;
  right: 40px;
  width: 30px;
  height: 30px;
  background-color: red;
  cursor: pointer;
}

.menu__logo {
  position: absolute;
  top: 27px;
  left: 50%;
  transform: translate(-50%, 0%);
}

.menu__logo img {
  width: 60px;
  height: 60px;
  transition: all 0.5s ease;
  transform-origin: center;
  transform-box: fill-box;
  animation: rotate 3s linear infinite;
}

.menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin: auto 0;
  padding: 120px 0 30px 0;
  overflow: hidden;
}

.menu__list-items {
  position: relative;
  margin: 0 0 15px 22px;
  background: transparent;
  height: 60px;
  width: 115px;
  padding: 0 0 0 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  transition: all 0.5s ease;
}

.menu__list-items:last-child {
  margin: 0 0 0 22px;
}

.menu__list-items svg {
  width: 25px;
  height: 25px;
}

.menu__list-items .menu-icons {
  fill: #fff;
}

.menu__list-items-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 0.3s ease;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #f5f7fb;
}

.menu__list-items-active {
  background: #f5f7fb;
  border-radius: 32px 0px 0px 32px;
}

.menu__list-items-active::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -22px;
  background: #625dee;
  border-radius: 0 0 30px 0;
  width: 100%;
  height: 30px;
  z-index: 5;
}

.menu__list-items-active::after {
  content: "";
  position: absolute;
  bottom: -50%;
  left: -22px;
  background: #625dee;
  border-radius: 0 30px 0 0;
  width: 100%;
  height: 30px;
  z-index: 5;
}

.menu__list-items-active .menu-icons {
  fill: #625dee;
}

.menu__list-items-active .menu__list-items-text {
  color: #222b45;
}

.menu__list-items-active .menu__list-items-add {
  position: absolute;
  top: -50%;
  right: 0;
  width: 60px;
  height: 120px;
  background: #f5f7fb;
}

.t-header__nav {
