}

.t-header__nav-client-notif {
  width: 20px;
  height: 26px;
  cursor: pointer;
  position: relative;
  margin: 0 23px 0 63px;
}

.t-header__nav-client-notif::after {
  content: "";
  position: absolute;
  top: -2px;
  right: -2px;
  background: #5447c8;
  width: 11.5px;
  height: 11.5px;
  border-radius: 50%;
}

.t-header__nav-client-notif svg {
  width: 20px;
  height: 26px;
}

.t-header__nav-client-profile {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  position: relative;
}

.t-header__nav-client-profile-photo {
  background: #c5cee0;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}

.t-header__nav-client-profile-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.t-header__nav-client-profile-name {
  margin: 0 0 0 11.5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.t-header__nav-client-profile-name h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  color: #222b45;
}

.t-header__nav-client-profile-name p {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #7b91b0;
}

.t-header__nav-client-arrow {
  margin: 0 0 0 25px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 10px;
  transition: all 0.5s ease;
}

.t-header__nav-client-list {
  position: absolute;
  bottom: calc(-100% - 5px);
  right: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
  justify-content: flex-start;
  border-radius: 15px 15px 0 0;
}

.t-header__nav-client-btn {
  width: 100%;
  background: #fff;
  padding: 12px 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #edf2f6;
}

.t-header__nav-client-btn:first-child {
  border-radius: 10px 10px 0 0;
}

.t-header__nav-client-btn:last-child {
  border-radius: 0 0 10px 10px;
  border-bottom: none;
}

.t-header__nav-client-btn p {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #222b45;
}

