  content: "";
  background: url("../svg/t-list/arrow-filter.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translate(0, -50%);
  height: 12px;
  width: 16px;
}

