  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.js-select-css .sort-select-block {
  bottom: auto;
  top: calc(100% + 1px);
  position: absolute;
  left: 0;
  z-index: 5;
}

