.quiz-select-css .quiz-select-placeholder:after {
  content: "";
  background: url("../svg/s-search/arrow-downs.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translate(0, -50%);
  height: 13px;
  width: 23px;
}

.quiz-select-css.quiz-select-item-active .quiz-select-placeholder:after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.quiz-select-css .quiz-select-block {
  bottom: auto;
  top: calc(100% + 1px);
  position: absolute;
  left: 0;
  z-index: 5;
}

