.s-search__quiz-long-items-radio__label:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid #625dee;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 2px;
