#quiz__time {
  border: none;
  color: #2a2c2d;
  font-size: 14px;
  font-family: helvetica;
  width: 111px;
  padding: 0 5px 0 5px;
  border: 1px solid #625dee;
  border-radius: 26px;
  cursor: pointer;
}

#quiz__time:focus {
  background: transparent;
}

/* Wrapper around the hour, minute, second, and am/pm fields as well as 
the up and down buttons and the 'X' button */

#quiz__time::-webkit-datetime-edit-fields-wrapper {
  display: flex;
}

/* The space between the fields - between hour and minute, the minute and 
second, second and am/pm */

#quiz__time::-webkit-datetime-edit-text {
  padding: 7px 2px;
}

/* The naming convention for the hour, minute, second, and am/pm field is
`-webkit-datetime-edit-{field}-field` */

/* Hour */

#quiz__time::-webkit-datetime-edit-hour-field {
  padding: 7px;
}

/* Minute */

#quiz__time::-webkit-datetime-edit-minute-field {
  padding: 7px;
}

/* AM/PM */

#quiz__time::-webkit-datetime-edit-ampm-field {
  background-color: #7155d3;
  border-radius: 15%;
  color: #fff;
  padding: 5px 13px;
}

/* 'X' button for resetting/clearing time */

#quiz__time::-webkit-clear-button {
  display: none;
}

/* Up/Down arrows for incrementing/decrementing the value */

#quiz__time::-webkit-inner-spin-button {
  display: none;
}

